.multipleChoiceContainer {
  background-color: beige;
  margin: 2rem 15% 0 10%;
}

/* Custom checkbox made with this
https://piped.kavin.rocks/watch?v=y1ATUFJyLaI tutorial.

"Style Checkbox Input Field with CSS" by Before Semicolon

Don't worry, piped is a Youtube alternative frontend
optimized for privacy, and piped.kavin.rocks is one piped
instance. The link goes to piped where you can watch
Youtube video, without Google recording your every move.
*/

.custom-checkbox {
  width: 25px;
  height: 25px;
  cursor: pointer;
  margin-left: 1rem;
  position: relative;
  top: 0.3rem;
}

.custom-checkbox .checkmark {
  width: 100%;
  height: 100%;
  border: 2px solid var(--main-color);
  display: inline-block;
  background: var(--main-color) url(../images/1200px-White_check.svg.png)
    center/1250% no-repeat;
}

* {
  box-sizing: border-box;
}

.custom-checkbox input:checked + .checkmark {
  background-size: 60%;
}

.custom-checkbox input {
  display: none;
}

.checkbox-group-item-success {
  background-color: var(--success-color);
}

.checkbox-group-item-error {
  background-color: var(--error-color);
}

.question-header {
  display: flex;
  flex-direction: row;
  margin-bottom: 1em;
}

.badge {
  color: black;
  text-align: center;
  font-size: large;
  margin: 0em 0em 0em 0.5em;
}

.badge.success {
  background-color: var(--success-color);
}

.badge.error {
  background-color: var(--error-color);
}
